<template>
  <div class="data-table">
    <a-table :columns="obj.columns"
             :pagination="obj.pagination"
             :loading="obj.loading"
             :rowKey="record => record.pid"
             :dataSource="obj.tabdata"
             :customRow="(res) => {
               return {
                 on: {
                   //click: (e) => {
                   //  this.rowHandler(e,res)
                   //}
                 }
               }
             }"
             @change="handleTableChange">
      <div slot="status"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a-switch checkedChildren="可采集"
                  unCheckedChildren="未采集"
                  :defaultChecked="text.status == '1'"
                  @change="switchBtn(text, record, index)" />
      </div>
      <div slot="siteLink"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a :href="text.address"
           target="_blank">{{text.address}}</a>
        <!-- <a-switch checkedChildren="可采集"
                  unCheckedChildren="未采集"
                  :defaultChecked="text.status == '1'"
                  @change="switchBtn(text, record, index)" /> -->
      </div>
      <div slot="remark"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <textarea name=""
                  id=""
                  :value="text.remark"
                  @keyup.enter="remarkHandler(text)"></textarea>
        <!-- <input :value="text.remark" type="text"
               @keyup.enter="remarkHandler(text)"> -->
      </div>
      <div slot="rolu"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a-button type="primary"
                  size="small"
                  @click="configuration(text)">
          配置信息
        </a-button>
        <!-- <input :value="text.remark" type="text"
               @keyup.enter="remarkHandler(text)"> -->
      </div>
      <div slot="detection"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a-button type="primary"
                  size="small"
                  @click="monitor(text)">
          监测
        </a-button>
        <!-- <input :value="text.remark" type="text"
               @keyup.enter="remarkHandler(text)"> -->
      </div>
      <div slot="delete"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a-button type="danger"
                  size="small"
                  @click="delectSite(text, index)">
          删除
        </a-button>
      </div>
    </a-table>
    <a-modal title="信息配置 "
             centered
             okText="确认"
             cancelText="取消"
             v-model="conInfo"
             @ok="conCommit">
      <a-form :form="form">
        <a-form-item v-bind="formItemLayout"
                     label="过滤规则">
          <a-input v-decorator="['filter_rule']" />
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     label="采集深度">
          <a-input v-decorator="['deep_limit']" />
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     label="选择器">
          <a-select v-decorator="['selector']">
            <a-select-option value="xpath">
              Xpath选择器
            </a-select-option>
            <a-select-option value="regexp">
              正则表达式
            </a-select-option>
            <a-select-option value="css">
              Css选择器
            </a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     style="margin-bottom: 0px"
                     label="采集字段">
        </a-form-item>
        <div class="add-bg">
          <a-form-item v-bind="childLayout"
                       label="标题">
            <a-input v-decorator="['title']" />
          </a-form-item>
          <a-form-item v-bind="childLayout"
                       label="作者">
            <a-input v-decorator="['author']" />
          </a-form-item>
          <a-form-item v-bind="childLayout"
                       label="时间">
            <a-input v-decorator="['publishTime']" />
          </a-form-item>
          <a-form-item v-bind="childLayout"
                       label="内容">
            <a-input v-decorator="['content']"></a-input>
          </a-form-item>
        </div>
      </a-form>

    </a-modal>
    <a-modal title="监测内容详情"
             :bodyStyle="{padding: 0}"
             v-model="monitorModal"
             okText="确认"
             cancelText="取消">
      <p class="monitor">
        <span>监测网址:</span>
        {{monitorData.url}}
      </p>
      <p class="monitor">
        <span>监测用时:</span>
        {{Number(monitorData.spending_time).toFixed(2)}}秒
      </p>
      <p class="monitor">
        <span>监测数据条数:</span>
        {{monitorData.count}}条
      </p>
    </a-modal>
  </div>
</template>

<script>
export default {
  name: 'Table',
  props: ['obj'],
  data () {
    return {
      pagination: { current: 1 },
      monitorModal: false,
      switchState: null,
      status: 0,
      pid: null,
      conInfo: false,
      formItemLayout: {
        labelCol: {
          xs: { span: 6 }
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 }
        }
      },
      childLayout: {
        labelCol: {
          xs: { span: 6 }
        },
        wrapperCol: {
          xs: { span: 20 },
          sm: { span: 14 }
        }
      },
      form: this.$form.createForm(this),
      fieldsValue: '{}',
      monitorData: {}
    }
  },
  mounted () {
  },
  methods: {
    rowHandler (e, res) {
      let pid = res.pid
      var parentPage = this.$parent.PagingObj
      parentPage.pid = pid
      this.$emit('rowClick', parentPage)
    },
    handleTableChange (pagination, filters, sorter) {
      var obj = this.$parent.searchObj
      const pager = { ...this.pagination }
      pager.current = pagination.current
      this.pagination = pager
      obj.page = pager.current
      this.$parent.siteAll(obj)
    },
    // 状态按钮改变事件
    switchBtn (text, record, index) {
      // console.log(event.target.innerText)
      var status = event.target.innerText === '可采集' ? 0 : 1
      this.status = status
      // console.log(text.remark)
      this.statusChange(text.pid, status)
    },
    // 状态按钮改变事件
    statusChange (pid, status) {
      // console.log(pid)
      status = status + '' || null
      // remark = remark || null
      let url = `${this.baseURL}/web_site/update?pid=${pid}&status=${status}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 提交备注
    remarkHandler (text) {
      let val = event.target.value
      text.remark = val
      let url = `${this.baseURL}/web_site/update?pid=${text.pid}&remark=${val}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 配置信息
    configuration (text) {
      this.conInfo = true
      var jsonParse = JSON.parse(text.rule)
      this.pid = text.pid
      setTimeout(() => {
        if (!text.rule) {
          this.form.setFieldsValue({
            filter_rule: '',
            selector: 'xpath选择器',
            deep_limit: '',
            title: '',
            author: '',
            publishTime: '',
            content: ''
          })
        } else {
          this.form.setFieldsValue({
            filter_rule: jsonParse.filter_rule,
            selector: jsonParse.selector,
            deep_limit: jsonParse.deep_limit === '' ? 1 : jsonParse.deep_limit,
            title: jsonParse.fields.title || '',
            author: jsonParse.fields.author,
            publishTime: jsonParse.fields.publishTime,
            content: jsonParse.fields.content
          })
        }
      }, 0)
    },
    // 配置信息提交
    conCommit () {
      this.form.validateFields((err, fieldsValue) => {
        if (err) {
          return
        }
        console.log(fieldsValue.selector)
        let obj = {
          filter_rule: fieldsValue.filter_rule,
          selector: fieldsValue.selector,
          deep_limit: fieldsValue.deep_limit,
          fields: {
            title: fieldsValue.title,
            author: fieldsValue.author,
            publishTime: fieldsValue.publishTime,
            content: fieldsValue.content
          }
        }
        console.log(fieldsValue.filter_rule)
        // var ruleRep = fieldsValue.filter_rule.replace('+', '@')
        obj.filter_rule = fieldsValue.filter_rule
        const values = encodeURIComponent(JSON.stringify(obj))
        // console.log(values)
        var url = `${this.baseURL}/web_site/update?pid=${this.pid}&rule=${values}`
        this.$http.get(url).then((res) => {
          this.handleTableChange(this.pagination)
          this.conInfo = false
        })
      })
    },
    // 监测
    monitor (text) {
      console.log(text)
      // var str = encodeURIComponent(text.rule)
      // console.log(str1)
      let url = `${this.baseURL}/web_site/spider?url=${text.address}&rule=${text.rule}`
      this.$http.get(url).then((res) => {
        this.monitorModal = true
        this.monitorData = res.data
      }, (reject) => {
        console.log(reject)
      })
    },
    // 删除网站
    delectSite (text, index) {
      let this_ = this
      this.$confirm({
        title: '删除',
        content: `您确定要删除名称为 “${text.webSite}” 的网站吗?`,
        okText: '确认',
        cancelText: '取消',
        onOk () {
          let url = `${this_.baseURL}/web_site/delete?pid=${text.pid}`
          this_.$http.get(url).then((res) => {
            this_.$parent.tabdata.splice(index, 1)
          })
        },
        onCancel () { }
      })
    },
    // 空事件阻止冒泡
    bubbling () {
      // console.log(1111)
    },
    // 子table的模态框
    showModal () {
      this.visible = true
    }
  },
  watch: {
    obj: {
      handler (newV, oldV) {
      },
      deep: true
    },
    switchState: {
      handler () {
        console.log(2222)
      }
    }
  },
  computed: {
    rowSelection () {
      // const selectedRowKeys = this
      return {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
        },
        getCheckboxProps: record => ({
          props: {
            // disabled: record.name === 'Disabled User', // Column configuration not to be checked
            // name: record.name
          }
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.data-table {
  padding: 0 1%;
}

.monitor {
  margin: 10px 0;
  padding-left: 30px;
}

.monitor span {
  font-weight: 500;
  font-size: 16px;
}

.add-bg >>> .ant-form-item {
  margin-bottom: 10px;
}
</style>



