<template>
  <div class="keywordTab">
    <div class="tabTop">
      <a-button type="primary">
        <a-icon type="plus-circle" /> 添加
      </a-button>
      <a-button type="danger">
        <a-icon type="delete" /> 删除
      </a-button>
    </div>
    <!-- <a-table :columns="columns"
             :dataSource="data"
             bordered>
      <template slot="name"
                slot-scope="text">
        <a style="color:#f00">{{ text }}</a>
      </template>
    </a-table> -->
    <div class="tabWrap">
      <table class="kwTable">
        <tbody>
          <tr>
            <th>关键词名</th>
            <th>关键词组</th>
          </tr>
          <tr v-for="(item,index) in tableData"
              :key="index">
            <td width="150">{{item.key_name}}</td>
            <td class="pd0">
              <div class="keylistBox">
                <div class="keylist"
                     v-for="(childItem,childIndex) in item.key_words_list"
                     :key="childIndex">
                  <span>{{childItem.key}}</span>
                  <div class="tags">
                    <a v-for="(thirdItem,thirdIndex) in childItem.words_list"
                       :key="thirdIndex">{{thirdItem}}
                      <i title="删除">x</i>
                    </a>
                  </div>
                  <div class="operate">
                    <a>添加</a>
                  </div>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="pages">
        <a-pagination v-model="current"
                      @change="onChange"
                      :pageSize="pageSize"
                      :total="total" />
      </div>
    </div>

  </div>
</template>

<script>
import $axios from '../../../../utils/request.js'
export default {
  name: 'KeyWord',
  data () {
    return {
      tableData: [],
      current: 1,
      limit: 5,
      pageSize: 5,
      page: 1,
      total: 0
    }
  },
  mounted () {
    this.keyWordFn()
  },
  methods: {
    keyWordFn () {
      let url = `${this.baseURL}/key_words/select?page=${this.page}&limit=${this.limit}`
      console.log($axios)
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          this.total = res.data.count
          this.tableData = res.data.data
        }
      })
    },
    onChange (current) { // 页码改变
      this.current = current
      this.page = current
      this.keyWordFn()
    }
  }
}
</script>
<style scoped>
.tabTop {
  padding: 20px;
  text-align: right;
}
.tabWrap {
  padding: 0 16px;
}
.kwTable {
  width: 100%;
}
.kwTable th {
  padding: 16px 16px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
  text-align: left;
  background: #fafafa;
  border: 1px solid #e8e8e8;
}
.kwTable td {
  padding: 0 16px;
  line-height: 35px;
  border: 1px solid #e8e8e8;
}
.kwTable td.pd0 {
  padding: 0;
}
.keylist {
  display: flex;
  position: relative;
}
.keylist + .keylist {
  border-top: 1px solid #e8e8e8;
}
.keylist span {
  border-right: 1px solid #e8e8e8;
  padding: 0 16px;
}
.tags {
  display: inline-block;
  padding: 5px 16px;
}
.tags a {
  margin-right: 15px;
  background: #72bcff;
  border: 1px solid #1890ff;
  padding: 4px 6px;
  font-size: 12px;
  border-radius: 3px;
  color: #fff;
  position: relative;
}
.tags a i {
  display: inline-block;
  width: 15px;
  height: 15px;
  text-align: center;
  background: #f00;
  color: #fff;
  border-radius: 50%;
  line-height: 15px;
  font-size: 12px;
  position: absolute;
  right: -9px;
  top: -8px;
  z-index: 2;
  display: none;
}
.tags a:hover i {
  display: block;
}
.operate {
  position: absolute;
  right: 16px;
  top: 0;
  bottom: 0;
  border-left: 1px solid #e8e8e8;
  padding-left: 16px;
  line-height: 48px;
}
.pages {
  text-align: right;
  margin: 16px 0;
}
</style>



