<template>
  <component :is="comName"></component>
</template>

<script>
import QuestionList from './questionList'
import QuestionReported from './questionReported'
export default {
  name: 'resourceitem',
  props: ['comName'],
  components: {
    QuestionList,
    QuestionReported
  }
}
</script>

<style lang="stylus" scoped>
</style>



