<template>
  <div class="project-manage">
    <a-button type="primary" @click="onDetailModal">项目管理</a-button>
    <div class="item-detail">
      <a-modal title="项目详情"
               v-model="itemDetailModal"
               okText="确认"
               cancelText="取消">
        <h3>项目名称:你好世界</h3>
        <p>项目描述:你好世界</p>
        <p>数据引擎信息:你好世界</p>
      </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UrlList',
  data () {
    return {
      itemDetailModal: true
    }
  },
  methods: {
    onDetailModal () {
      this.itemDetailModal = true
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



