<template>
  <div class="data-list">
    <div class="search-inp">
      <input type="text"
             v-model="inpValue"
             placeholder="请输入站点">
      <a-button type="primary"
                icon="search"
                @click="searchBtn">
      </a-button>
    </div>
    <div class="data-tab">
      <a-table :dataSource="dataList"
               :pagination='{total: count}'
               :rowKey="record => record.collection_time"
               @change="PageChange"
               :columns="columns">
        <div slot="siteLink"
             slot-scope="text, record, index">
          <a :href="text.url"
             target="_blank">{{text.url}}</a>
        </div>
        <div slot="publishTime"
             slot-scope="text, record, index">
          <span>{{timeTransition(text.collection_time)}}</span>
        </div>
        <div slot="summary"
             slot-scope="text, record, index">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <p class="hoverCon">{{text.summary}}</p>
            </template>
            <span class="summaryCon">{{text.summary}}</span>
          </a-popover>
        </div>
        <div slot="detailsBtn"
             slot-scope="text, record, index">
          <a-button type="primary"
                    @click="onLookDetail(text)">
            详情
          </a-button>
        </div>
      </a-table>
    </div>
    <!-- 详情内容 -->
    <a-modal title="详情列表"
             :bodyStyle="{padding: 0}"
             width="60%"
             v-model="detailModel">
      <h3 class="detail-tit">{{detailInfo.title}}</h3>
      <div class="detail-info">
        <span class="auth">{{detailInfo.authors}}</span>
        <span class="time">时间: {{timeTransition(detailInfo.publish_time)}}</span>
      </div>
      <div class="url-path">
        新闻链接:
        <a :href="detailInfo.url"
           target="_back">{{detailInfo.url}}</a>
      </div>
      <p class="detail-con"
         v-html='detailInfo.content'></p>
    </a-modal>
  </div>
</template>

<script>
let columns = [
  {
    title: '网址',
    scopedSlots: { customRender: 'siteLink' },
    width: '15%',
    align: 'center'
  },
  {
    title: '标题',
    dataIndex: 'title',
    width: '10%',
    align: 'center'
  },
  {
    title: '作者',
    dataIndex: 'authors',
    width: '5%',
    align: 'center'
  },
  {
    title: '采集时间',
    scopedSlots: { customRender: 'publishTime' },
    width: '10%',
    align: 'center'
  },
  {
    title: '数据来源',
    dataIndex: 'source',
    width: '10%',
    align: 'center'
  },
  {
    title: '摘要',
    scopedSlots: { customRender: 'summary' },
    width: '25%',
    align: 'center'
  },
  {
    title: '查看内容',
    scopedSlots: { customRender: 'detailsBtn' },
    width: '7%',
    align: 'center'
  }
]
export default {
  name: 'dataList',
  data () {
    return {
      dataList: [],
      count: 1,
      page: 1,
      inpValue: '',
      detailModel: false,
      detailInfo: {},
      columns
    }
  },
  mounted () {
    this.allReptileData()
  },
  methods: {
    PageChange (page) {
      this.page = page.current
      this.allReptileData()
    },
    allReptileData () {
      let url = `${this.baseURL}/new/datas?page=${this.page}&limit=10&keywords=${this.inpValue}`
      this.$http.get(url).then((res) => {
        console.log(res)
        this.dataList = res.data.data
        this.count = res.data.count
      })
    },
    timeTransition (time) {
      var d = new Date(time)
      var datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' +
        d.getDate() + ' ' + lessThan(d.getHours()) + ':' +
        lessThan(d.getMinutes()) + ':' + lessThan(d.getSeconds())
      function lessThan (num) {
        if (num < 10) {
          return '0' + num
        } else {
          return num
        }
      }
      return datetime
    },
    searchBtn () {
      this.allReptileData()
    },
    confirm () {
      // this.message.info('Clicked on Yes.')
    },
    // 查看详情
    onLookDetail (text) {
      this.detailModel = true
      this.detailInfo = text
      console.log(text)
    }
    // 详情
  }
}
</script>

<style lang="stylus" scoped>
.data-list {
  .search-inp {
    height: 60px;
    line-height: 60px;
    text-align: center;

    input {
      height: 34px;
      border: 1px solid #ccc;
      text-indent: 0.2rem;
    }
  }
}

.hoverCon {
  width: 300px;
}

.summaryCon {
  display: block;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.detail-tit, .detail-info, .detail-con {
  text-align: center;
}

.detail-tit {
  font-weight: 700;
  font-size: 26px;
  padding: 10px 0;
}

.detail-con {
  height: 500px;
  overflow: auto;
  text-align: left;
  padding: 0 120px;
}

.detail-info {
  span {
    padding: 0 50px;
  }
}

.url-path {
  text-align: left;
  padding-left: 120px;
  margin: 20px 0 20px 0;
}

.data-list >>> .ant-table {
  table-layout: fixed;
}

.data-list >>> .ant-table-tbody > tr > td {
  max-width: 110px;
  border-bottom: 0;
  text-align: center !important;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>



