<template>
  <div class="tastList">
    <div class="operationBtn">
      <a-button type="primary"
                @click="onAddTask">
        <a-icon type="plus-circle" /> 添加
      </a-button>
      <a-button type="primary"
                class="refresh"
                @click="onRefresh">
        手动刷新
      </a-button>
    </div>
    <a-table bordered
             :dataSource="taskList"
             :columns="columns"
             :pagination='paging'
             @change="pagingChange"
             :style="{height: '635px'}"
             rowKey='id'>
      <div slot="delete"
           class="delete"
           slot-scope="text, record, index">
        <a-button type="danger"
                  size="small"
                  @click="onDelectTask(text, index)">
          删除
        </a-button>
        <a-button type="primary"
                  size="small"
                  @click="onStartTask(text, index)">
          启动
        </a-button>
        <a-button type="default"
                  size="small"
                  @click="onStopTask(text, index)">
          终止
        </a-button>
      </div>
      <div slot="lead"
           slot-scope="text, record, index">
        <a-button type="primary"
                  size="small"
                  @click="onLeadTask(text, index)">
          配置
        </a-button>
      </div>
    </a-table>
    <a-modal title="添加爬虫任务"
             v-model="Addmodel"
             @ok="addPresent"
             :centered='true'
             okText="确认"
             cancelText="取消">
      <a-form>
        <a-form-item label="名称"
                     :label-col="{ span: 5 }"
                     :wrapper-col="{ span: 12 }">
          <a-input placeholder="请输入名称"
                   v-model="taskName" />
        </a-form-item>
      </a-form>
    </a-modal>
    <a-modal title="导入任务"
             width="80%"
             v-model="Leadmodel"
             :centered='true'
             footer=''>
      <div class="leadBtn">
        <a-button type="primary"
                  @click="onLeadList">
          <a-icon type="rise" /> 确定
        </a-button>
        <a-select defaultValue="0"
                  style="width: 140px"
                  @change="selectList">
          <a-select-option :value="item.id"
                           v-for="(item, index) in dataType"
                           :key="index">
            {{item.value}}
          </a-select-option>
        </a-select>
      </div>
      <a-table :columns="leadList"
               :rowSelection="{
                  selectedRowKeys: selectedRowKeys,
                  onChange: onSelectChange
                }"
               :pagination='Leadpage'
               rowKey="pid"
               @change="LeadPageChange"
               :dataSource="LeadData">
      </a-table>
    </a-modal>
    <a-modal title="配置信息"
             width="80%"
             okText="导入"
             @ok="onLeadBtn"
             okType="default"
             @cancel="submitBtn"
             cancelText="提交"
             v-model="LeadListModel">
      <a-form>
        <a-form-item label="名称"
                     :label-col="{ span: 9 }"
                     :wrapper-col="{ span: 7 }">
          <a-input placeholder="请输入名称" />
        </a-form-item>
      </a-form>
      <DataTable :dataId="id"
                 @currentChange="getCurrent"
                 ref="childTable"></DataTable>
    </a-modal>
  </div>
</template>

<script>
import DataTable from './dataTable.vue'

let leadList = [
  {
    title: '网址',
    dataIndex: 'address',
    width: '30%',
    align: 'center'
  },
  {
    title: '网络名称',
    dataIndex: 'webSite',
    width: '30%',
    align: 'center'
  },
  {
    title: '网站类型',
    // dataIndex: 'sort',
    width: '30%',
    align: 'center',
    customRender: (text, row, index) => {
      switch (text.sort) {
        case 0:
          return '新闻'
        case 1:
          return '论坛(社交网络)'
        case 2:
          return '微博'
        case 3:
          return '平面媒体'
        case 5:
          return '博客'
        case 7:
          return '其他(搜索引擎)'
        case 9:
          return '推特'
        case 10:
          return '微信'
        case 11:
          return '招标'
        case 12:
          return '手机App'
        case 13:
          return '外闻网站'
        case 15:
          return 'instagram'
        case 16:
          return 'facebook'
        case 17:
          return 'twitter'
      }
    }
  }
]
let dataType = [
  { id: '0', value: '新闻' },
  { id: '1', value: '论坛(社交网络)' },
  { id: '2', value: '微博' },
  { id: '3', value: '平面媒体' },
  { id: '5', value: '博客' },
  { id: '7', value: '其他(搜索引擎)' },
  { id: '9', value: '推特' },
  { id: '10', value: '微信' },
  { id: '11', value: '招标' },
  { id: '12', value: '手机App' },
  { id: '13', value: '外闻网站' },
  { id: '15', value: 'instagram' },
  { id: '16', value: 'facebook' },
  { id: '17', value: 'twitter' }
]
export default {
  name: 'reptileList',
  components: {
    DataTable
  },
  data () {
    let columns = [
      {
        title: '爬虫名称',
        dataIndex: 'task_name',
        width: '30%',
        align: 'center',
        scopedSlots: { customRender: 'name' }
      }, {
        title: '状态',
        dataIndex: 'status',
        customRender: (text, row, index) => {
          return text === 0 ? <span style="color: #f5222d">未检测</span> : <span style="color: #1890ff">已检测</span>
          // return <a href="javascript:;">ddd</a>
          // return text.status === 0 ? '未检测' : <span></span>
        },
        align: 'center'
      }, {
        title: '时间',
        customRender: (text, row, index) => {
          return text.create_time
        },
        align: 'center'
      }, {
        title: '导入',
        align: 'center',
        scopedSlots: { customRender: 'lead' }
      }, {
        title: '操作',
        align: 'center',
        scopedSlots: { customRender: 'delete' }
      }
    ]
    return {
      dataType,
      taskList: [],
      LeadData: [],
      pagination: { total: 0 },
      Leadpaging: {
        total: 0
      },
      taskParams: { 'page': 1, 'limit': 10, 'keyword': '' },
      leadPage: 1,
      Addmodel: false,
      Leadmodel: false,
      LeadListModel: false,
      taskName: '',
      LeadObj: {},
      columns,
      leadList,
      selectedRowKeys: [],
      selectedItem: [],
      newObj: {},
      dataList: [],
      dataListCount: 0,
      TaskPage: 1,
      selectVal: 0,
      id: 1
    }
  },
  mounted () {
    this.AllTaskList()
  },
  methods: {
    // 下拉选项
    selectList (e) {
      this.selectVal = e
      this.onLeadBtn()
    },
    // 页数改变
    pagingChange (page) {
      let present = page.current
      this.taskParams.page = present
      this.AllTaskList()
    },
    // 导出列表页数改变
    LeadPageChange (page) {
      let present = page.current
      this.leadPage = present
      this.onLeadBtn()
    },
    // 关联所有的选中项
    onSelectChange (selectedRowKeys, selectedItem) {
      console.log(selectedRowKeys)
      this.selectedRowKeys = selectedRowKeys
    },
    // 导出选中的任务列表
    onLeadList () {
      console.log(this.selectedRowKeys)
      let url = `${this.baseURL}/task_config/spider_name?task_id=${this.id}&operation=import&main_url_pids=[${this.selectedRowKeys}]`
      this.$http.get(url).then((res) => {
        this.LeadListModel = true
        this.Leadmodel = false
        this.$refs.childTable.getDataList()
      })
    },
    // 搜有爬虫任务列表
    AllTaskList () {
      let obj = this.taskParams
      let url = `${this.baseURL}/task/select?
                page=${obj.page}&limit=${obj.limit}
                &keyword=${obj.keyword}`.replace(/\s/g, '')
      this.$http.get(url).then((res) => {
        console.log(res)
        this.pagination.total = res.data.count
        this.taskList = res.data.data
      })
    },
    // 添加爬虫任务
    onAddTask () {
      this.Addmodel = true
    },
    // 删除爬虫任务
    onDelectTask (text, index) {
      let url = `${this.baseURL}/task_config/select_by_id?id=${text.id}&page=1&limit=10`
      this.$http.get(url).then((res) => {
        let len = res.data.data.length
        console.log(len)
        if (len > 0) {
          this.warning('配置中网址信息未全部移除,不能删除')
        } else {
          this.showConfirm('删除任务', '您确定要删除此任务', text.id)
        }
      })
    },
    // 启动任务
    onStartTask (text, index) {
      if (text.status === 0) {
        let url = `${this.baseURL}/start/spider_task?id=${text.id}`
        this.$http.get(url).then((res) => {
          this.success('任务启动成功')
          this.AllTaskList()
        })
      } else {
        this.warning('该任务正在后台进行中，请勿重复启动')
      }
    },
    // 终止任务
    onStopTask (text, index) {
      if (text.status === 1) {
        let url = `${this.baseURL}/stop/spider_task?id=${text.id}`
        this.$http.get(url).then((res) => {
          this.success('任务终止成功')
          this.AllTaskList()
        })
      } else {
        this.warning('该任务正已处在终止状态，请勿重复终止')
      }
    },
    // 确定提交
    addPresent () {
      let url = `${this.baseURL}/task/add?task_name=${this.taskName}`
      this.$http.get(url).then((res) => {
        if (res.status === 200) {
          this.success('添加成功')
          this.Addmodel = false
          this.AllTaskList()
        }
      }, (reject) => {
      })
    },
    // 导入任务配置
    onLeadTask (test) {
      this.LeadListModel = true
      this.id = test.id
    },
    // 获取子组件传过来的current
    getCurrent (cur) {
      this.TaskPage = cur
      this.onLeadTask()
    },
    // 导入事件
    onLeadBtn () {
      this.Leadmodel = true
      let url = `${this.baseURL}/task_config/select_all?page=${this.leadPage}&limit=10&sort=${this.selectVal}`
      this.$http.get(url).then((res) => {
        console.log(res.data)
        this.LeadData = res.data.data
        this.Leadpaging.total = res.data.count
      })
    },
    // 提交事件
    submitBtn (e) {
      let eleName = e.target.tagName.toLowerCase()
      if (eleName === 'button') {
        let removePidArr = this.$refs.childTable.removePidArr
        let url = `${this.baseURL}/task_config/spider_name?task_id=${this.id}&operation=remove&main_url_pids=[${removePidArr}]`
        this.$http.get(url).then((res) => {
          this.$refs.childTable.removePidArr = []
          this.$refs.childTable.page = 1
          this.$refs.childTable.pagination.current = 1
          this.$refs.childTable.getDataList()
        })
      }
    },
    // 手动刷新
    onRefresh () {
      this.AllTaskList()
    },
    // 确认对话框
    showConfirm (title, content, id) {
      let this_ = this
      this.$confirm({
        title: title,
        content: content,
        cancelText: '取消',
        okText: '确认',
        onOk () {
          let url = `${this_.baseURL}/task/delete?id=${id}`
          this_.$http.get(url).then((res) => {
            this_.success('删除成功')
            this_.AllTaskList()
          }, (reject) => {
            this_.error('删除失败')
          })
        },
        onCancel () { }
      })
    },
    // 成功提示
    success (text) {
      this.$message.success(text)
    },
    warning (text) {
      this.$message.config({
        top: '20%',
        duration: 2,
        maxCount: 3
      })
      this.$message.warning(text)
    }
  },
  watch: {
  },
  computed: {
    paging: function () {
      return this.pagination
    },
    Leadpage: function () {
      return this.Leadpaging
    }
  }
}
</script>

<style lang="stylus" scoped>
.tastList {
  .operationBtn {
    padding: 20px 20px;

    .refresh {
      float: right;
    }
  }

  .delete >>> .ant-btn-sm {
    margin: 0 20px;
  }
}
</style>



