<template>
  <component :is="comName"></component>
</template>

<script>
import DataStatistics from './statistics'
import DataList from './dataList'
import system from './systemMon'
export default {
  name: 'resourceitem',
  props: ['comName'],
  components: {
    DataStatistics,
    DataList,
    system
  }
}
</script>

<style lang="stylus" scoped>
</style>



