<template>
  <div class="system-mon">
    <div class="title">
      <p>电脑系统监控数据图表</p>
    </div>
    <div class="echart-map">
      <div id="chart-cpu"
           style="width: 30%;height:367px"></div>
      <div id="chart-disk"
           style="width: 30%;height:367px"></div>
      <div id="chart-internal"
           style="width: 30%;height:367px"></div>
    </div>
    <div class="echart-map">
      <div id="server-One-cpu"
           style="width: 30%;height:367px"></div>
      <div id="server-One-disk"
           style="width: 30%;height:367px"></div>
      <div id="server-One-internal"
           style="width: 30%;height:367px"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'systemMon',
  data () {
    return {
      monitor: []
    }
  },
  mounted () {
    this.monitoring()
    this.serverOne()
  },
  methods: {
    monitoring () {
      let url = `${this.baseURL}/system/info`
      this.$http.get(url).then((res) => {
        if (res.status === 200) {
          this.monitor = res.data
          this.echartCpu(this.monitor.cpu, 'server-One-cpu')
          this.echartDisk(this.monitor.memory, 'server-One-internal')
          this.echartInternal(this.monitor.disk, 'server-One-disk')
        }
      }, (reject) => {
        console.log(reject)
      })
    },
    serverOne () {
      let url = `http://101.132.113.50:5000/system/info`
      this.$http.get(url).then((res) => {
        console.log(res)
        if (res.status === 200) {
          let dataInfo = res.data
          this.echartCpu(dataInfo.cpu, 'chart-cpu')
          this.echartDisk(dataInfo.memory, 'chart-internal')
          this.echartInternal(dataInfo.disk, 'chart-disk')
        }
      })
    },
    echartCpu (cpu, el) {
      const option = {
        title: {
          text: cpu.cpu_count + ' 核',
          textStyle: {
            color: '#235894'
          }
        },
        markPoint: {
          symbolSize: 5
        },
        tooltip: {},
        series: [{
          name: '四核',
          type: 'pie',
          selectedMode: 'single',
          center: ['50%', '50%'],
          // radius : '40%',
          selectedOffset: 1,
          clockwise: true,
          label: {
            normal: {
              position: 'inner',
              textStyle: {
                fontSize: 14,
                color: '#fff'
              }
            }
          },

          labelLine: {
            normal: {
              lineStyle: {
                color: '#235894'
              }
            }
          },
          data: [
            { value: cpu.cpu_percent, name: '已使用' },
            { value: 100 - cpu.cpu_percent, name: '未使用' }
          ]
        }]
      }
      this.phase(option, el)
    },
    echartInternal (memory, el) {
      const option = {
        title: {
          text: '磁盘使用数据' + memory.percent + '%',
          textStyle: {
            color: '#235894'
          }
        },
        markPoint: {
          symbolSize: 10
        },
        tooltip: {},
        series: [{
          name: '磁盘',
          type: 'pie',
          selectedMode: 'single',
          center: ['50%', '50%'],
          // radius : '40%',
          selectedOffset: 30,
          clockwise: true,
          label: {
            normal: {
              position: 'inner',
              textStyle: {
                fontSize: 14,
                color: '#fff'
              }
            }
          },

          labelLine: {
            normal: {
              lineStyle: {
                color: '#235894'
              }
            }
          },
          data: [
            { value: (memory.total - memory.free) / memory.total * 100, name: '已使用' },
            { value: memory.free / memory.total * 100, name: '未使用' }
          ]
        }]
      }
      this.phase(option, el)
    },
    echartDisk (disk, el) {
      const option = {
        title: {
          text: '内存使用',
          textStyle: {
            color: '#235894'
          }
        },
        markPoint: {
          symbolSize: 10
        },
        tooltip: {},
        series: [{
          name: '内存',
          type: 'pie',
          selectedMode: 'single',
          center: ['50%', '50%'],
          // radius : '40%',
          selectedOffset: 30,
          clockwise: true,
          label: {
            normal: {
              position: 'inner',
              textStyle: {
                fontSize: 14,
                color: '#fff'
              }
            }
          },

          labelLine: {
            normal: {
              lineStyle: {
                color: '#235894'
              }
            }
          },
          data: [
            { value: (disk.total - disk.free) / disk.total * 100, name: '已使用' },
            { value: disk.free / disk.total * 100, name: '未使用' }
          ]
        }]
      }
      this.phase(option, el)
    },
    phase (option, el) {
      const myChart = this.$echarts.init(document.getElementById(el))
      myChart.setOption(option)
      window.addEventListener('resize', function () {
        myChart.resize()
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
.system-mon {
  .echart-map {
    display: flex;
    justify-content: space-around;
  }

  .title {
    p {
      padding: 20px;
      text-align: center;
      font-size: 36px;
    }
  }
}
</style>



