<template>
  <div class="statistics">
    <div class="title">
      <p>数据统计图表</p>
    </div>
    <div class="sta-echart">
      <div id="statis-map" style="width: 100%;height:367px"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'statistics',
  mounted () {
    this.echartStatis()
  },
  methods: {
    echartStatis () {
      let option = {
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          data: [820, 932, 901, 934, 1290, 1330, 1320],
          type: 'line',
          smooth: true
        }]
      }
      this.phase(option, 'statis-map')
    },
    phase (option, el) {
      const myChart = this.$echarts.init(document.getElementById(el))
      myChart.setOption(option)
      window.addEventListener('resize', function () {
        myChart.resize()
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
  .statistics{
    .title{
      p{
        text-align: center;
        font-size: 32px;
        padding: 20px 0;
      }
    }
  }
</style>



