<template>
  <div class="data-table">
    <a-table :columns="leadList"
             :pagination= pagination
             rowKey="pid"
             @change="PageChange"
             :dataSource="dataList">
      <div slot="delete"
           slot-scope="text, record, index">
        <a-button type="danger"
                  size="small"
                  @click="removeItem(text, index, record.key, record.pid)">
          移除
        </a-button>
      </div>
    </a-table>
  </div>
</template>

<script>
let leadList = [
  {
    title: '网址',
    dataIndex: 'address',
    width: '30%',
    align: 'center'
  },
  {
    title: '网络名称',
    dataIndex: 'webSite',
    width: '30%',
    align: 'center'
  },
  {
    title: '操作',
    scopedSlots: { customRender: 'delete' },
    width: '30%',
    align: 'center'
  }
]
export default {
  name: 'dataTable',
  props: {
    dataId: {
      type: Number || String
    }
  },
  data () {
    return {
      pagination: {defaultCurrent: 1, current: 1, total: 20},
      Leadpage: {},
      count: '',
      removePidArr: [],
      dataList: [],
      page: 1,
      leadList
    }
  },
  created () {
  },
  mounted () {
    this.getDataList()
  },
  methods: {
    getLeadList (list) {
      this.NewLeadData = this.NewLeadData.concat(list)
      console.log(this.NewLeadData)
    },
    getDataList () {
      let url = `${this.baseURL}/task_config/select_by_id?id=${this.dataId}&page=${this.page}&limit=10`
      this.$http.get(url).then((res) => {
        console.log(res)
        let arr = res.data.data
        let newArr = arr.map((item, index) => {
          item.key = index
          return item
        })
        this.dataList = newArr
        this.pagination.total = res.data.count
      })
    },
    PageChange (page) {
      this.page = page.current
      this.pagination.current = this.page
      this.getDataList()
    },
    removeItem (text, index, key, pid) {
      this.removePidArr.push(pid)
      const dataSource = [...this.dataList]
      //  通过过滤的方法进行移除
      this.dataList = dataSource.filter(item => item.key !== key)
      // this.Newcount = this.NewLeadData.length
    }
  },
  watch: {
    dataId: function () {
      console.log(this.dataId)
      this.pagination.current = 1
      this.page = 1
      this.getDataList()
    }
    // Leadmodel: function (oldValue, newValue) {
    // },
    // LeadData: function () {
    //   this.NewLeadData = this.LeadData
    // }
  },
  computed: {
  }
}
</script>

<style lang="stylus" scoped>
.data-table >>> .ant-table-tbody > tr > td {
  padding: 8px 16px;
}

.data-table {
  position: relative;
  height: auto;
}
</style>



