<template>
  <div class="home">
    <a-layout-header class="hd-tit">
      <div class="name">
        <span>爬虫管理系统</span>
        <a-icon class="trigger"
                :type="collapsed ? 'menu-unfold' : 'menu-fold'"
                @click="()=> collapsed = !collapsed" />
      </div>
      <div class="account">
        <a-dropdown>
          <a-menu slot="overlay"
                  @click="handleMenuClick">
            <a-menu-item key="1">
              <a-icon type="user" />个人信息</a-menu-item>
            <a-menu-item key="2">
              <a-icon type="user" />账户切换</a-menu-item>
            <a-menu-item key="3">
              <a-icon type="user" />退出</a-menu-item>
          </a-menu>
          <a-button style="margin-left: 8px">
            admin
            <a-icon type="down"/>
          </a-button>
        </a-dropdown>
      </div>
    </a-layout-header>
    <a-layout id="components-layout-demo-custom-trigger">
      <a-layout-sider :trigger="null"
                      collapsible
                      v-model="collapsed">
        <a-menu
          mode="inline"
          theme="dark"
          :defaultSelectedKeys="['1']"
          :defaultOpenKeys="['sub1']"
          :style="{ height: '100%', borderRight: 0 }"
          >
          <a-sub-menu key="sub1">
            <span slot="title"><a-icon type="user"/><span v-if="!collapsed">网址管理</span></span>
            <a-menu-item key="1" @click="componentName('SiteM')">站点管理</a-menu-item>
            <a-menu-item key="2" @click="componentName('KeyWord')">关键词</a-menu-item>
            <!-- <a-menu-item key="2" @click="componentName('ProManage')">项目管理</a-menu-item> -->
          </a-sub-menu>
          <a-sub-menu key="sub2">
            <span slot="title"><a-icon type="laptop"/><span v-if="!collapsed">爬虫管理</span></span>
            <a-menu-item key="3" @click="onReptileTask">爬虫任务</a-menu-item>
          </a-sub-menu>
          <a-sub-menu key="sub3">
            <span slot="title"><a-icon type="laptop"/><span v-if="!collapsed">资源管理</span></span>
            <a-menu-item key="4" @click="onStstem('system')">系统监控</a-menu-item>
            <a-menu-item key="5" @click="onStstem('DataStatistics')">数据统计</a-menu-item>
            <a-menu-item key="6" @click="onStstem('DataList')">数据列表</a-menu-item>
          </a-sub-menu>
          <!-- <a-sub-menu key="sub4">
            <span slot="title"><a-icon type="laptop"/><span v-if="!collapsed">问题管理</span></span>
            <a-menu-item key="7" @click="onQuestion('questionList')">问题列表</a-menu-item>
            <a-menu-item key="8" @click="onQuestion('questionReported')">问题上报</a-menu-item>
          </a-sub-menu> -->
        </a-menu>
      </a-layout-sider>
      <a-layout>
        <a-layout-content :style="{ margin: '24px 16px', padding: 0, background: '#fff', minHeight: '400px' }"
                          :is="currentView"
                          :comName="name"
        >
        </a-layout-content>
      </a-layout>
    </a-layout>
  </div>
</template>

<script>
import UrlManage from '../UrlManage/UrlManage'
import ReptileManage from '../reptileManage/reptileList/reptileList'
import SystemMon from '../resource/resource'
import QuestionMamage from '../questionManage'
export default {
  name: 'Home',
  components: {
    UrlManage,
    ReptileManage,
    SystemMon,
    QuestionMamage
  },
  data () {
    return {
      collapsed: false,
      name: 'SiteM',
      currentView: 'UrlManage'
    }
  },
  methods: {
    handleButtonClick (e) {
      console.log('click left button', e)
    },
    handleMenuClick (e) {
      console.log('click', e)
    },
    componentName (name) {
      this.name = name
      this.currentView = UrlManage
    },
    onReptileTask () {
      this.currentView = ReptileManage
    },
    onStstem (name) {
      this.currentView = SystemMon
      this.name = name
    }
    // onQuestion (name) {
    //   this.currentView = QuestionMamage
    //   this.name = name
    // }
  }
}
</script>

<style lang="stylus" scoped>
.home {
  position: relative;
  height: 100%;
  width: 100%;

  .hd-tit {
    position: absolute;
    left: 0;
    top: 0;
    height: 65px;
    width: 100%;
    background-color: #fff;

    .name {
      float: left;
    }

    .account {
      float: right;
    }
  }
}
#components-layout-demo-custom-trigger {
  width: 100%;
  height: 100%;
  padding-top: 65px;
}

#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}

#components-layout-demo-custom-trigger .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}
</style>



